/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { useLocation } from "@reach/router";
import { useStaticQuery, graphql } from "gatsby";
import cloneScriptImg from "../styles/assets/images/og-image/matrimony-clone-script.png";
function Seo(props) {
  const {
    title,
    metaTitle: metaTitleProp,
    description,
    keywords,
    image,
    children,
  } = props;
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            keywords
            author
            siteUrl
          }
        }
      }
    `
  );

  const location = useLocation();
  console.log("location::", location);

  const defaultTitle = site.siteMetadata?.title;
  const metaTitle = metaTitleProp || defaultTitle;
  const metaDescription = description || site.siteMetadata.description;
  const metaKeywords = keywords || site.siteMetadata.keywords;
  const ogImage = image;
  const currentUrl = location?.href || site.siteMetadata.siteUrl;

  const imageMap = [
    {
      route: "/clone-script/",
      image: cloneScriptImg,
    },
  ];

  const currentImage = imageMap.find((e) => location.pathname === e?.route);
  console.log("currentImage::", currentImage);

  return (
    <Fragment>
      <title>{title ? title : defaultTitle}</title>
      <meta name="title" content={metaTitle} />
      <meta name="description" content={metaDescription} />
      <meta property="og:locale" content={"en_US"} />
      <meta property="og:title" content={metaTitle} />
      <meta property="og:description" content={metaDescription} />
      <meta property="og:image" content={site.siteMetadata.siteUrl + ogImage} />
      <meta property="og:keywords" content={metaKeywords} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={currentUrl} />
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:creator" content={site.siteMetadata?.author || ``} />
      <meta name="twitter:title" content={metaTitle} />
      <meta name="twitter:description" content={metaDescription} />
      <meta
        name="robots"
        content="index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1"
      />
      {children}
    </Fragment>
  );
}

Seo.defaultProps = {
  description: ``,
};

Seo.propTypes = {
  description: PropTypes.string,
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
};

export default Seo;
